// Import the LitElement base class and html helper function
import { html, css } from 'lit-element'
import { VfesCustom } from '../custom';

// Extend the LitElement base class
export class VfesSwitchItem extends VfesCustom {
  static get properties () {
    return {
      name: {
        type: String,
        attribute: true
      },
      checked: {
        type: Boolean,
        attribute: true
      },
      disabled: { type: Boolean },
      size: { type: String }
    }
  }

  static get styles() {
    return css`
    :host {
      width: 100%;
    }`;
  }

  getSize () {
    return 'vfes-form__switch--' + this.size
  }

  updateSwitch (e) {
    this.checked = e.target.checked
  }

  get template () {
    const id = 'checkbox-' + (Math.floor(Math.random() * 9000000000) + 1000000000)
    return html`
    <input 
      @change="${this.updateSwitch}"
      class="vfes-ws10 vfes-form__switch ${this.getSize()}" 
      type="checkbox" 
      id="${id}" 
      .name="${this.name}" 
      .checked="${this.checked}"
      ?disabled=${this.disabled}>
    <label class="vfes-form__switch-label" for="${id}">
      <slot></slot>
    </label>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-switch-item', VfesSwitchItem)
