// Import the LitElement base class and html helper function
import { html, css, svg } from 'lit-element'
import { VfesCustom } from '../custom'
import '../icon'

// Extend the LitElement base class
export class VfesInputItem extends VfesCustom {
  static get properties () {
    return {
      id: {
        type: String,
        attribute: true
      },
      name: {
        type: String,
        attribute: true
      },
      value: { 
        type: String,
        attribute: true
      },
      disabled: { type: Boolean },
      ariaLabelled: { type: String },
      placeholder: { type: String },
      state: { type: String },
      idLabel: { type: String }
    }
  }

  constructor () {
    super()
    this.ariaLabelled = ''
    this.placeholder = ''
    this.value = ''
    this.state = ''
  }

  static get styles() {
    return css`
    :host {
      width: 100%;
    }`;
  }

  getState () {
    return this.state ? 'vfes-form__input--' + this.state : ''
  }

  updateInput (e) {
    this.value = e.target.value
  }

  get template () {
    return html`
    <input 
      @change="${this.updateInput}"
      class="vfes-ws10 vfes-form__input ${this.getState()}"
      type="text" 
      .id="${this.id}" 
      .name="${this.name}" 
      .value="${this.value}"
      ?disabled=${this.disabled}
      aria-labelled="${this.ariaLabelled}"
      placeholder="${this.placeholder}">
    <span class="vfes-form__label" id="${this.idLabel}">
      <slot name="label"></slot>
    </span>
    <p class="vfes-form__error-text">
      <slot name="errorValidation"></slot>
    </p>
    <vfes-icon 
      class="vfes-form__icon vfes-form__icon-warning" 
      width='32px' 
      height='32px' 
      title="Warning" icon='icon3-warning'></vfes-icon>
    <vfes-icon 
    class="vfes-form__icon vfes-form__icon-tick" 
    width='32px' 
    height='32px' 
    title="Warning" icon='icon3-tick-or-solved'></vfes-icon>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-input-item', VfesInputItem)
