// Import the LitElement base class and html helper function
import { html } from 'lit-element'
import { VfesCustom } from '../custom'

// Extend the LitElement base class
export class VfesAvatar extends VfesCustom {
  static get properties () {
    return {
      bgColor: { type: String },
      size: { type: String }
    }
  }

  constructor () {
    super()
    this.bgColor = 'dove-gray'
  }

  get template () {
    return html`
      <figure class="vfes-avatar vfes-avatar__icon-${this.size} vfes-color-bg-${this.bgColor}">
          <slot></slot>
      </figure>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-avatar', VfesAvatar)
