// Import the LitElement base class and html helper function
import { html } from 'lit-element'
import { ifDefined } from 'lit-html/directives/if-defined.js'
import { VfesCustom } from '../custom'

// Extend the LitElement base class
export class VfesButton extends VfesCustom {
  static get properties () {
    return {
      type: { type: String },
      text: { type: String },
      name: { type: String },
      title: { type: String },
      disabled: { type: Boolean },
      analitycsElement: { type: String },
      analitycsContext: { type: String }
    }
  }

  get template() {
    return html`
      <button 
        type="button" 
        .name="${ifDefined(this.name)}" 
        title="${this.title}" 
        class="vfes-ws10 vfes-button vfes-button--${this.type}" 
        data-analytics-element="${ifDefined(this.analitycsElement)}"
        data-analytics-context="${ifDefined(this.analitycsContext)}"
        data-analytics-category="cta" 
        ?disabled="${this.disabled}">
          <slot></slot>
      </button>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-button', VfesButton)
