// Import the LitElement base class and html helper function
import { html, css, svg, unsafeCSS } from 'lit-element'
import { VfesCustom } from './custom'

// Extend the LitElement base class
export class VfesIcon extends VfesCustom {
  static get properties () {
    return {
      title: { type: String },
      class: { type: String },
      icon: { type: String },
      height: { type: String },
      width: { type: String }
    }
  }

  static get styles() {    
    return css`
      :host { 
        height: ${unsafeCSS(this.height)};
      }
    `;
  } 

  firstUpdated () {
    const namespace = 'http://www.w3.org/1999/xlink'
    const svgPath = '/public/svg/sprite.v3.svg'
    let useelement = this.shadowRoot.querySelector('use')
    useelement.setAttributeNS(namespace, 'xlink:href', svgPath + '#' + this.icon)
  }

  get template () {
    return html`
      ${svg`<svg class="${this.class}" height="${this.height}" width="${this.width}" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
        <title>${this.title}</title>
        <use class="icon-v3" xlink:href=""></use>
      </svg>`}
      `
  }
}
// Register the new element with the browser.
customElements.define('vfes-icon', VfesIcon)
