// Import the LitElement base class and html helper function
import { html } from 'lit-element'
import { VfesCustom } from '../custom'
import Carousel from '../../es/_carousel'

// Extend the LitElement base class
export class VfesCarousel extends VfesCustom {
  static get properties () {
    return {
      config: { type: Object }
    }
  }

  firstUpdated () {
    this.updateComplete.then(() => {
      const carouselItems = this.querySelectorAll('vfes-carousel-item')
      const slides = Array.from(carouselItems).map(item => item.shadowRoot.querySelector('li'))
      this.instance = new Carousel({
        $element: this.shadowRoot.getElementById('carousel'),
        config: JSON.stringify(this.config),
        listItems: slides
      })
      this.instance.init()
    })
  }

  constructor () {
    super()
    this.config = {
      responsive: {
        0: {
          breakpoint: 0,
          elements: 1
        },
        1: {
          breakpoint: 640,
          elements: 2
        },
        2: {
          breakpoint: 970,
          elements: 3
        }
      },
      build: {
        arrows: true,
        bullets: true,
        clickable_bullets: true
      }
    }
  }

  get template () {
    return html`
      <div>
        <div id="carousel" class='vfes-carousel'>
          <ul class='vfes-carousel__list js-carousel-list'>
            <slot></slot>
          </ul>
        </div>
    </div>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-carousel', VfesCarousel)
