// Import the LitElement base class and html helper function
import { html, css } from 'lit-element'
import { VfesCustom } from '../custom'

// Extend the LitElement base class
export class VfesAvatarImage extends VfesCustom {
  static get properties () {
    return {
      src: { type: String },
      alt: { type: String }
    }
  }

  static get styles() {
    return css`
    :host {
      align-self: flex-end;
    }`;
  }

  get template () {
    return html`
    <img class="vfes-avatar__img vfes-avatar--align vfes-flex__align-self--end" src="${this.src}" alt="${this.alt}"/>
    `
  }
}
// Register the new element with the browser.
customElements.define('vfes-avatar-image', VfesAvatarImage)
